<template>
    <div>
        <el-row>
            <el-col class="search-bar">
                <el-input placeholder="请输入车型" class="w_200" icon="search" v-model="searchWord" :on-icon-click="search"></el-input>
                <div class="m_l_20">
                    <el-button @click="sortTable">{{orderBy == 1? '正': '倒'}}序排列</el-button>
                </div>
                <div class="m_l_20">
                    <el-button @click="exportExc" :loading="exportLoading" icon='document'>导出Excel</el-button>
                </div>
            </el-col>
        </el-row>
        <el-row>
            <el-col>
                <el-table class="table" v-loading="listLoading" :data="tableData" border width="100%" :max-height="tableMaxHeight">
                    <el-table-column align="center" label="序号" width="80">
                        <template scope="scope">
                            {{(scope.$index+1) + pageSize * (currentPage-1)}}
                        </template>
                    </el-table-column>
                    <el-table-column align="center" prop="car_uuid" label="车辆ID">
                    </el-table-column>
                    <el-table-column align="center" prop="user_uuid" label="用户ID">
                    </el-table-column>
                    <el-table-column align="center" label="最后登录时间">
                        <template scope="scope">
                            {{getTime(scope.row.updatetime && scope.row.updatetime.time)}}
                        </template>
                    </el-table-column>
                    <el-table-column align="center" prop="car.modelCharacter" label="车型">
                    </el-table-column>
                    <el-table-column align="center" prop="car.color" label="颜色">
                    </el-table-column>
                    <el-table-column align="center" prop="car.address" label="取车位置">
                    </el-table-column>
                    <el-table-column align="center" prop="hot" label="需求热度">
                    </el-table-column>
                    <el-table-column align="center" label="状态" width="120">
                        <template scope="scope">
                            {{getCarStatus(scope.row.car.carStatus)}}
                        </template>
                    </el-table-column>
                    <el-table-column align="center" prop="ordercount" label="订单数">
                    </el-table-column>
                    <el-table-column align="center" prop="rate" label="差价利润">
                    </el-table-column>
                    <el-table-column align="center" prop="price" label="上架价格">
                    </el-table-column>
                    <el-table-column align="center" label="操作" width="260">
                        <template scope="scope">
                            <el-button type="info" size="small" @click="editItem(scope.$index, scope.row)">查看详情</el-button>
                            <el-button type="warning" size="small" @click="changestatus(scope.$index, scope.row)" :disabled="scope.row.carStatus == 4 ? true : false">{{scope.row.carStatus == 4 ?'已冻结' : '冻　结'}}</el-button>
                            <el-button size="small" type="danger" @click="deleteItem(scope.$index, scope.row)" icon='delete2'>删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>

        <el-row>
            <el-col>
                <div class="block pagination">
                    <el-pagination :current-page.sync="currentPage" @current-change="handleCurrentChange" :page-size="pageSize" layout="prev, pager, next, jumper" :page-count="total">
                    </el-pagination>
                </div>
            </el-col>
        </el-row>
    
        <el-dialog class="edit dialog-info" size="large" :visible.sync="editDialog">
            <div class="info-content">
                <el-row>
                    <el-col>用户：<span>{{eForm.userName}}</span></el-col>
                    <el-col>车主：<span>{{eForm.carOwnerName}}</span></el-col>
                    <el-col>车辆ID：<span>{{eForm.carUuid}}</span></el-col>
                    <el-col>车型：<span>{{eForm.model}}</span></el-col>
                    <el-col>车牌：<span>{{eForm.licensePlateNumber}}</span></el-col>
                    <el-col>颜色：<span>{{eForm.color}}</span></el-col>
                    <el-col>注册时间：<span>{{getTime(eForm.registerTime && eForm.registerTime.time)}}</span></el-col>
                    <el-col>排量：<span>{{eForm.displacement}}</span></el-col>
                    <el-col>发动机号：<span>{{eForm.engineNumber}}</span></el-col>
                    <el-col>车架号：<span>{{eForm.vehicleIdentificationNumber}}</span></el-col>
                    <el-col>变速箱：<span>{{eForm.manualOrAutomatic == 1? '自动' : eForm.manualOrAutomatic == 2? '手动' : eForm.manualOrAutomatic == 3? '手自一体' : '--'}}</span></el-col>
                    <el-col>年款：<span>{{eForm.modelYear}}</span></el-col>
                    <el-col>座位数：<span>{{eForm.seatNumber}}</span></el-col>
                    <el-col>类型：<span>{{eForm.carType == 1? '出租': eForm.carType == 2? '日常': '--'}}</span></el-col>
                    <el-col>城市：<span>{{eForm.city}}</span></el-col>
                    <el-col>接单量：<span>{{eForm.orderQuantity}}</span></el-col>
                    <el-col>
                        罚款数：<span>{{eForm.userID}}</span>
                        <!-- <el-button type="text">查看</el-button> -->
                    </el-col>
                    <el-col>
                        出租价格：
                        <span>周末{{eForm.weekendPrice}}</span>
                        &nbsp;&nbsp;
                        <span>工作日{{eForm.workingDayPrice}}</span>
                    </el-col>
                    <el-col :span="12">
                        人车合影：<img class="photo" :src="eForm.manCarPhoto">
                    </el-col>
                    <el-col :span="12">
                        行驶证照片：<img class="photo" :src="eForm.drivingLicensePhoto">
                    </el-col>
                    <el-col>上传时间：<span>{{formatTime(eForm)}}</span></el-col>
                </el-row>
            </div>

            <p class="info-title">车辆配置：</p>
            <div class="info-content">
                <el-row>
                    <el-col :span="5">
                        交车方式：<span>{{formatVehicleDeliveryMode(eForm.vehicleDeliveryMode)}}</span>
                    </el-col>
                    <el-col :span="5">
                        行车里程：<span>{{eForm.mileage}}</span>
                    </el-col>
                    <el-col :span="5">
                        车龄：<span>{{eForm.vehicleAge}}</span>
                    </el-col>
                    <el-col :span="5">
                        是否有导航：<span>{{eForm.isNavigation == 1? '有' : eForm.isNavigation == 2? '无' : '--'}}</span>
                    </el-col>
                    <el-col :span="5">
                        是否能外接mp3：<span>{{eForm.isMp3 == 1? '能' : eForm.isMp3 == 2? '不能' : '--'}}</span>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col>
                        车辆描述：<span>{{eForm.carDsp}}</span>
                    </el-col>
                </el-row>
            </div>

            <div class="info-radio">
                <el-radio-group v-model="eForm.carStatus">
                    <el-radio class="radio" :label="1">审核中</el-radio>
                    <el-radio class="radio" :label="2">审核成功</el-radio>
                    <el-radio class="radio" :label="3">审核未通过</el-radio>
                    <el-radio class="radio" :label="4">冻结</el-radio>
                    <el-radio class="radio" :label="5">驳回</el-radio>
                </el-radio-group>
            </div>

            <div slot="footer" class="dialog-footer">
                <el-button @click="editDialog = false">取 消</el-button>
                <el-button type="primary" @click="editSave('editForm')">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
export default {
    data() {
        return {
            word: '',
            searchWord: '',
            orderBy: 2,
            listLoading: false,
            exportLoading: false,
            tableData: [],
            eForm: {},
            editDialog: false, //弹出框-编辑
            radio: '1',
            // 分页
            currentPage: 1,
            pageSize: 20,
            total: 0,
            // 表格距离顶部的高度
            tableTop: 0,
        }
    },
    props: ['API'],
    computed: {
        // 计算表格高度
        tableMaxHeight() {
            this.$store.commit('setTableMaxHeight', this.tableTop)
            return this.$store.state.tableMaxHeight
        }
    },
    methods: {
        search(ev) {
            this.word = this.searchWord
            this.currentPage = 1
            this.getList(this.word);
        },
        // 排序
        sortTable() {
            this.orderBy = this.orderBy == 1? 2: 1
            this.getList(this.word)
        },
        // 格式化
            // 格式化时间
            formatTime(row, column) {
                return this.$common.getNowTime(row.createAt && row.createAt.time)
            },
            // 格式化时间2
            getTime(time) {
                return this.$common.getNowTime(time)
            },
            // 格式化状态
            formatStatus(row, column) {
                return row.managerStatus == 1 ? '激活' : row.managerStatus == 2 ? '冻结' : '--'
            },
            getCarStatus(status) {
                switch (status) {
                    case 1: return '审核中'
                    case 2: return '审核成功'
                    case 3: return '审核未通过'
                    case 4: return '冻结'
                    case 5: return '驳回'
                    default: return '--'
                }
            },
            // 交付方式
            formatVehicleDeliveryMode(status) {
                switch (status) {
                    case 2: return '自助交车'
                    case 0: 
                    case undefined: 
                    case null: 
                    case 1: return '当面交车'
                    default: return '--'
                }
                // return status? (status == 2? '自助交车' : status == 1? '当面交车' : '--') : '当面交车'
            },
        editItem(index, row) {
            var json = {
                "head": this.$common.getUserStorage(),
                "body": {
                   "carUuid": row.car_uuid
                }
            }
            this.$common._post('car/detail', json, this, data => {
                this.editDialog = true
                this.eForm= Object.assign({}, data.car)
                this.eForm.userName = data.userName
            })
        },
        // 编辑保存
        editSave(formName){
            this.$confirm('是否确认修改?', '提示', {})
                .then(() => {
                    var json = {
                        "head": this.$common.getUserStorage(),
                        "body": {
                           "carUuid": this.eForm.carUuid,
                           "carStatus": this.eForm.carStatus
                        }
                    }
                    this.$common._post('car/updateCarStatus', json, this, data => {
                        this.editDialog = false
                        this.$message({ type: 'success',message: data.description })
                        this.getList()
                    })
                }).catch(() => {})
        },
        deleteItem(index, row) {
            var json = {
                "head": this.$common.getUserStorage(),
                "body": {
                   "carUuid": row.car_uuid
                }
            }
            this.$common.deleteItem(this.API + 'car/delete', this, json)
        },
        // 冻结
        changestatus(index, row) {
            this.$confirm('是否确认修改?', '提示', {})
                .then(() => {
                    var json = {
                        "head": this.$common.getUserStorage(),
                        "body": {
                           "carUuid": row.car_uuid,
                           "carStatus": 4
                        }
                    }
                    this.$common._post('car/updateCarStatus', json, this, data => {
                        this.editDialog = false
                        this.$message({ type: 'success',message: data.description })
                        this.getList()
                    })
                }).catch(() => {})
        },
        exportExc() {
            this.exportLoading = true
            var json = {
                "head": this.$common.getUserStorage(),
                "body": { }
            }
            this.$http.post(this.API + 'exportExcelCar', json).then(data=>{
                this.exportLoading = false
                var data = data.body
                if (data.body.result == '0') {
                    location.href = data.body.fileUrl
                } else {
                    this.$message(data.body.description)
                }
            })
        },
        handleCurrentChange(val) {
            this.currentPage = val;
            this.getList(this.word);
        },
        // 获取列表
        getList(modelCharacter = ''){
            this.listLoading = true
            var json = {
                "head": this.$common.getUserStorage(),
                "body": {
                   "pageIndex": this.currentPage,
                   "pageSize": this.pageSize,
                   "modelCharacter": modelCharacter,
                   "orderBy": this.orderBy,
                }
            }
            this.$common._post('car/list', json, this, data => {
                this.total = data.total
                this.tableData = data.carList
                this.listLoading = false
            }, data=>{
                this.listLoading = false
                this.tableData = []
            })
        }
    },
    mounted(){
        if (!sessionStorage.getItem('zhizunbao_login')) {
            this.$message('请登录账号')
            this.$router.push('/')
            return
        }
        this.getList()
        // 表格距离顶部的高度
        this.tableTop = (document.querySelector('#main table').getBoundingClientRect().top)
    }
}
</script>

<style scoped lang="scss">

</style>



